:root {
    --main-bg-color: rgba(39, 160, 95, 0.9);
    --main-link-color: rgb(39, 160, 95);
    --very-light-border: rgba(0, 0, 0, 0.1);
    --off-white-bg-color: rgb(250, 243, 235);
    --main-desktop-width: 70vw;
    --button-delete: rgb(248, 86, 86);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: var(--off-white-bg-color);
}

a {
    color: var(--main-link-color);
}

a:visited {
    color: var(--main-link-color);
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Josefin Sans', sans-serif;
    color: var(--main-link-color);
    margin: 8px 0;
}

h2 {
    line-height: 250%;
}

h3:not(.recipeHeaderInfo) {
    border-bottom: 2px solid black;
    line-height: 250%;
}

table {
    width: 100%;
}


input {
    border: 1px solid var(--main-link-color);
    border-radius: 10px;
    height: 5vh;
    padding: 10px;
    margin: 0 10px;
    margin-bottom: 1vh;
    font-size: 1.1rem;
}

input[type=submit] {
    background-color: var(--main-link-color);
    color: white;
}

textarea {
    border: 1px solid var(--main-link-color);
    border-radius: 10px;
    width: 100%;
    height: 9vh;
    padding: 10px;
    font-size: 1.05rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

button {
    border: 1px solid var(--main-link-color);
    border-radius: 10px;
    background-color: var(--main-link-color);
    color: white;
    width: 100%;
    height: 5vh;
    padding: 10px;
}

fieldset {
    border: 1px solid var(--main-link-color);
    padding: 10px 10px;
    margin-bottom: 3vh;
}

legend {
    padding: 3px 10px;
}

select {
    border: 1px solid var(--main-link-color);
    border-radius: 10px;
    width: 100%;
    height: 5vh;
    padding: 10px;
    margin-bottom: 3vh;
    font-size: 1.05rem;
}

/*
Classes -------------------------------
*/
.header {
    margin-bottom: 3vh;
    border-bottom: 2px solid black;
    padding: 5vh 5vw 3vh 5vw;
}

.headerContainer {
    display: flex;
    justify-content: space-between;
    width: var(--main-desktop-width);
    margin: 0 auto;
}

.breadcrumbsWrapper {
    width: var(--main-desktop-width);
    margin: 0 auto;
}

.icon {
    font-size: 1.25rem;
}

.navContainer li {
    list-style: none;
    display: inline-block;
    margin: 0 15px;
}

.categoriesContainer {
    padding: 0;
    margin: 100px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.categoriesListContainer {
    border: 3px solid var(--main-link-color);
    display: flex;
    justify-content: center;
    align-items: center;
    width: var(--main-desktop-width);
    font-size: 3.5rem;
    line-height: 1.6;
    padding: 5vh 0;
}

.categoriesListContainer li {
    list-style: none;
}

.recipe {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 20vh;
}

.recipeContainer {
    width: var(--main-desktop-width);
    padding: 10px;
    border-left: 1px solid var(--very-light-border);
    border-right: 1px solid var(--very-light-border);
}

.recipeContainer img {
    width: 35vw;
    height: 35vh;
    object-fit: cover;
    border: 2px solid black;
}

.recipeContainer ul {
    list-style: none;
}

.recipeContainer ol {
    /* font-family: 'Prata', serif; */
    margin-left: 20px;
}

.recipeContainer li {
    line-height: 175%;
}

.recipeHeaderContainer {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.recipeHeaderInfo {
    align-items: center;
    justify-content: center;
    /* margin: auto; */
    width: 35vw;
}

.recipeHeaderInfo span {
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 0.9rem;
    font-weight: 100;
}

.ingredientItem span {
    display: inline-block;
    white-space: nowrap;
    width: 12vw;
}

.quantity {
    width: 10vw;
}

.about {
    display: flex;
    justify-content: center;
    align-items: center;
}

.aboutContainer {
    width: var(--main-desktop-width);
    padding: 10px;
    border-left: 1px solid var(--very-light-border);
    border-right: 1px solid var(--very-light-border);
}

.submit {
    display: flex;
    justify-content: center;
    align-items: center;
}

.formContainer {
    width: var(--main-desktop-width);
    padding: 10px;
    border-left: 1px solid var(--very-light-border);
    border-right: 1px solid var(--very-light-border);
    margin-bottom: 10vh;
}

.formContainer li {
    list-style: none;
    display: inline-block;
    width: 100%;
}

.tips {
    display: flex;
    justify-content: center;
    align-items: center;
}

.tipsContainer {
    width: var(--main-desktop-width);
    padding: 10px;
    border-left: 1px solid var(--very-light-border);
    border-right: 1px solid var(--very-light-border);
}

.inputButtonDelete {
    width: 5vh;
    height: 5vh;
    color: white;
    background-color: var(--button-delete);
    justify-content: center;
    align-items: center;
}

.inputQuantity {
    width: 20%;
}

.inputIngredient {
    min-width: 67%;
}


/*
Mobile Layout ---------------------
*/

@media only screen and (max-width: 600px) {
    h1 {
        font-size: 1.45rem;
    }

    h2 {
        line-height: 150%;
    }

    .header {
        padding: 1vh 4vw;
    }

    .navContainer {
        font-size: 0.85rem;
    }

    .navContainer li {
        margin: 3px;
    }

    .breadcrumbsWrapper {
        width: 95vw;
        font-size: 0.9rem;
    }

    .headerContainer {
        width: 95vw;
    }

    .categoriesListContainer {
        width: 95vw;
        font-size: 1.5rem;
    }

    .recipeContainer {
        width: 100vw;
    }

    .recipeContainer img {
        max-width: 95vw;
        /* width: auto; */
        height: 35vh;
        min-width: 100%;
    }

    .recipeHeaderContainer {
        display: block;
        justify-content: space-around;
    }

    .recipeHeaderInfo {
        position: absolute;
        background-color: rgba(255, 255, 255, 0.7);
        height: 35vh;
        width: 50%;
        padding-left: 5vw;
        border-style: solid;
        border-width: 2px 0 2px 2px;
    }

    .recipeHeaderInfo span {
        font-weight:500
    }

    .recipeImg {
        width: auto;
    }

    .quantity {
        width: 30vw;
    }

    .formContainer {
        width: 100vw;
    }

}